.not-found {
    text-align: center;
    margin-top: 50px;
}

.not-found h1 {
    font-size: 6em;
    margin-bottom: 20px;
}

.not-found p {
    font-size: 1.5em;
    margin-bottom: 20px;
}

.not-found a {
    text-decoration: none;
    color: #007bff;
    font-size: 1.2em;
}

.not-found a:hover {
    text-decoration: underline;
}
